.About {
  width: 100%;
  height: 100%;
  padding: 120px 0;
  background-color: var(--secondary-background-color);
  box-sizing: border-box;
  transition: 2s all;
  overflow-x: hidden;
}

.about-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
.about-container .section-title{
  padding-top: 20px;
}
.about-container .section-title h2 span {
  font-size: 48px;
  background: linear-gradient(
    120deg,
    #1c99fe 20.69%,
    #7644ff 50.19%,
    #fd4766 79.69%
  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.about-container .section-title h2 {
  color: var(--primary-white-color);
  font-size: 50px;
  padding-top: 10px;
  text-align: center;
}

.section-title p {
  color: var(--primary-white-color);
  margin-top: 30px;
  font-weight: 300;
  font-size: 20px;
  letter-spacing: 3px;
  font-family: "Poppins", sans-serif;
  text-align: center;
}

.section-title p span {
  font-size: 20px;
  background: linear-gradient(
    120deg,
    #1c99fe 20.69%,
    #7644ff 50.19%,
    #fd4766 79.69%
  );
  background-clip: text;
  text-transform: capitalize;
  -webkit-text-fill-color: transparent;
  display: inline-block;
  text-transform: capitalize;
}
.about-info {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-top: 20px ;
}

.about-image img {
  position: relative;
  border-radius: 10px;
  height: 556px;
  max-width: 100%;
  z-index: 10;
  padding: 10px;
}

/* about me dashborad css */
.about-me-dashborad {
  width: 50vw;
  height: 556px;
}
.tabs {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.tablist {
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
  border: 1px solid #2d3548;
  border-radius: 95px;
  color: var(--primary-white-color);
}
.tabhead {
  color: var(--primary-white-color);
  cursor: pointer;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  font-size: 18px;
  padding: 15px 35px;
}

.active {
  background: linear-gradient(
    120deg,
    #1c99fe 20.69%,
    #7644ff 50.19%,
    #fd4766 79.69%
  );
  border-radius: 50px;
  padding: 15px 25px;
  margin-left:0;
}
.tabcontentlist {
  margin-top: 0px;
}

.skill-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.skill-container .heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
}
.skill-container .heading h2 {
  font-size: 16px;
  color: var(--primary-white-color);
  margin-top: 10px;
  padding-top: 10px;
}
.skill-container .heading .web {
  color: yellow;
  font-size: 18px;
  padding-top: 10px;
}
.progress-bar-container {
  width: 100%;
  height: 5px;
  position: relative;
  background: var(--primary-white-color);
  border-radius: 5px;
}
.progress-bar-container .progress-bar-1 {
  position: absolute;
  height: 100%;
  background: yellow;
  animation: progress-animation-1 5s forwards;
}
/* animtion for progress bar-1 */

@keyframes progress-animation-1 {
  0% {
    width: 0%;
  }
  100% {
    width: 98%;
    box-shadow: 0 40px 40px yellow;
  }
}

/* css for second progress bar */

.skill-container .heading .mobile {
  color: #1c99fe;
  font-size: 18px;
}

.progress-bar-container .progress-bar-2 {
  position: absolute;
  height: 100%;
  background: red;
  animation: progress-animation-2 3s forwards;
}

/* animtion for progress bar-2 */

@keyframes progress-animation-2 {
  0% {
    width: 0%;
  }
  100% {
    width: 90%;
    box-shadow: 0 0 40px red;
  }
}

.skill-container .heading .game {
  color: purple;
  font-size: 18px;
}

.progress-bar-container .progress-bar-3 {
  position: absolute;
  height: 100%;
  background: linear-gradient(
    120deg,
    #1c99fe 20.69%,
    #7644ff 50.19%,
    #fd4766 79.69%
  );
  animation: progress-animation-3 4s forwards;
}
/* animtion for progress bar-3 */

@keyframes progress-animation-3 {
  0% {
    width: 0%;
  }
  100% {
    width: 85%;
  }
}
.skill-container .heading .blockchain {
  color: green;
  font-size: 18px;
}
.progress-bar-container .progress-bar-4 {
  position: absolute;
  height: 100%;
  background: linear-gradient(
    120deg,
    #1c99fe 20.69%,
    #7644ff 50.19%,
    #fd4766 79.69%
  );
  animation: progress-animation-4 2.5s forwards;
}
/* animtion for progress bar-4 */

@keyframes progress-animation-4 {
  0% {
    width: 0%;
  }
  100% {
    width: 93%;
  }
}

.skill-container .heading .react {
  color: lightskyblue;
}
.progress-bar-container .progress-bar-5 {
  position: absolute;
  height: 100%;
  background: lightskyblue;
  animation: progress-animation-5 1s forwards;
}
/* animtion for progress bar-5 */

@keyframes progress-animation-5 {
  0% {
    width: 0%;
  }
  100% {
    width: 98%;
    box-shadow: 30px 0 40px lightskyblue;
  }
}
.skill-container .heading .angular {
  color: red;
}
.progress-bar-container .progress-bar-6 {
  position: absolute;
  height: 100%;
  background: rgb(238, 6, 6);
  animation: progress-animation-6 5s forwards;
}
/* animtion for progress bar-6 */

@keyframes progress-animation-6 {
  0% {
    width: 0%;
  }
  100% {
    width: 90%;
    box-shadow: 30px 0 40px rgb(248, 13, 189);
  }
}
.skill-container .heading .vue {
  color: rgb(16, 243, 35);
}
.progress-bar-container .progress-bar-7 {
  position: absolute;
  height: 100%;
  background: rgb(16, 243, 35);
  animation: progress-animation-7 3s forwards;
}
/* animtion for progress bar-7 */

@keyframes progress-animation-7 {
  0% {
    width: 0%;
  }
  100% {
    width: 93%;
    box-shadow: 0 0 40px rgb(16, 243, 35);
  }
}
.skill-container .heading .Next {
  color: rgb(6, 7, 7);
  font-size: 25px;
}
.progress-bar-container .progress-bar-8 {
  position: absolute;
  height: 100%;
  background: linear-gradient(
    120deg,
    #1c99fe 20.69%,
    #7644ff 50.19%,
    #fd4766 79.69%
  );
  animation: progress-animation-8 3s forwards;
}
/* animtion for progress bar-8 */

@keyframes progress-animation-8 {
  0% {
    width: 0%;
  }
  100% {
    width: 92%;
    box-shadow: 0 0 40px rgb(0, 3, 3);
  }
}

.skill-container .heading .gatsby {
  color: rgb(248, 15, 209);
}
.progress-bar-container .progress-bar-9 {
  position: absolute;
  height: 100%;
  background: rgb(224, 15, 207);
  animation: progress-animation-9 1s forwards;
}
/* animtion for progress bar-9 */

@keyframes progress-animation-9 {
  0% {
    width: 0%;
  }
  100% {
    width: 92%;
    box-shadow: 0 0 40px rgb(243, 5, 184);
  }
}
.skill-container .heading .node {
  color: rgb(30, 114, 4);
}
.progress-bar-container .progress-bar-10 {
  position: absolute;
  height: 100%;
  background: rgb(30, 114, 4);
  animation: progress-animation-10 1.5s forwards;
}
/* animtion for progress bar-10 */

@keyframes progress-animation-10 {
  0% {
    width: 0%;
  }
  100% {
    width: 97%;
    box-shadow: 0 0 40px rgb(5, 248, 38);
  }
}
.skill-container .heading .express {
  color: rgb(229, 240, 83);
}
.progress-bar-container .progress-bar-11 {
  position: absolute;
  height: 100%;
  background: rgb(235, 247, 75);
  animation: progress-animation-11 3.8s forwards;
}
/* animtion for progress bar-11 */

@keyframes progress-animation-11 {
  0% {
    width: 0%;
  }
  100% {
    width: 94%;
    box-shadow: 0 0 40px rgb(229, 240, 83);
  }
}
.skill-container .heading .mongo {
  color: rgb(22, 194, 31);
}
.progress-bar-container .progress-bar-12 {
  position: absolute;
  height: 100%;
  background: rgb(22, 194, 31);
  animation: progress-animation-12 2.5s forwards;
}
/* animtion for progress bar-12 */

@keyframes progress-animation-12 {
  0% {
    width: 0%;
  }
  100% {
    width: 95%;
    box-shadow: 0 0 40px rgb(229, 240, 83);
  }
}

.skill-container .heading .postgress {
  color: rgb(15, 220, 247);
}
.progress-bar-container .progress-bar-13 {
  position: absolute;
  height: 100%;
  background: rgb(15, 220, 247);
  animation: progress-animation-13 4s forwards;
}
/* animtion for progress bar-13 */

@keyframes progress-animation-13 {
  0% {
    width: 0%;
  }
  100% {
    width: 90%;
    box-shadow: 0 0 40px rgb(12, 250, 230);
  }
}
.tabcontent .experience {
