.navbar {
  left: 0;
  top: 0;
  position: absolute;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  padding: 20px 0 0 0;
  background-color: var(--transparent-color);
  z-index: 2;
  animation: navanimate 4.5s ease-in-out forwards;
}

@keyframes navanimate {
    0%{
      transform: translateY(-100px);
      opacity: 0;
      transform-style:preserve-3d;
    }
    100%{
      transform:translateY(0);
      opacity: 1;
      transform-style:flat;
      
    }
}

.nav-left {
  display: flex;
  justify-content: center;
  align-items: center;
}

.activate {
  background-color: var(--primary-background-color);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  height: 80px;
}

.logo {
  color: var(--primary-white-color);
  margin-left: 20px;
  font-size: 38px;
  cursor: pointer;
  z-index: 101;
}
.logo span {
  color: #f0092f;
  font-size: 38px;
  font-weight: bold;
  cursor: pointer;
}
.nav-menu {
  display: block;
  margin-left: 50px;
}
.nav-left .nav-menu ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav-menu ul li {
  padding: 0 0 0 50px;
  color: var(--primary-white-color);
  font-size: 18px;
}
.nav-menu ul li a {
  color: var(--primary-white-color);
}
.nav-menu ul li:hover {
  color: var(--primary-white-color);
  cursor: pointer;
}

.nav-right {
  display: flex;
  align-items: center;
  margin-right: 50px;
}
.social-share-inner ul {
  display: flex;
}
.social-share-inner ul li {
  padding: 0 15px 0 0;
  margin: 10px 40px 10px 0;
  color: var(--primary-white-color);
  font-size: 20px;
}
.social-share-inner ul li:hover {
  font-size: 28px;
  cursor: pointer;
}

.day{
  font-size: 35px;
  color: var(--primary-white-color);
  z-index: 999;
  
}
.day:hover{
  font-size: 40px;
  color: var(--primary-white-color);
  transition: all 0.5s;
}

.hamburger-menu {
  display: none;
}
.mobile-menu {
  display: none;
}
.mobile-day{
  display: none;
}




/* Media Quries for responsiveness */

@media screen and (max-width: 480px) {

  .nav-menu {
    display: none;
  }
  .social-share-inner {
    display: none;
  }
  .navbar{
    animation: none;
  }
  .day{
    display: none;
  }

  .contact-me button {
    display: none;
  }
  
  .hamburger-menu {
    display: block;
    color: var(--primary-white-color);
    z-index: 101;
    font-size: 25px;
  }
  .mobile-menu{
    width: 100%;
    height: 0%;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    color: var(--primary-white-color);
    background: blue;
  }
  .mobile-menu .mobile-nav{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
   padding: 10% 20%;

  }
  .mobile-nav li {
    padding: 20px 0 0 0;
    font-size: 25px;
    font-weight:500;
    font-family: 'popins', cursive;
  }
  .mobile-nav li a{
    color: var(--primary-white-color);
    font-size: 28px;
  }
  .mobile-nav li a:hover{
    color: skyblue;
    font-size: 28px;
    font-weight: bolder;
    transition: 0.5s all;
  }
  .social-handles {
    z-index: 101;
    color: var(--primary-white-color);
    padding: 8px 0;
  }
  .social-handles ul {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .social-handles ul li {
    padding: 0 20px;
    margin: 0 10px;
    font-size: 30px;
  }
  .social-handles ul li {
    border: 1px solid var(--primary-white-color);
    background: #f5f5f5;
    color: blue;
    border-radius: 10px;
    font-weight: bold;
  }
  .social-handles ul li:nth-child(1) {
    color: rgb(255, 10, 145);
  }

  
  .mobile-day{
    display: block;
    text-align: center;
    font-size: 40px;
    position: absolute;
    top: 2%;
    transition:all 1s;
  }
  .show {
    height: 90vh;
    transition: ease-in-out 1s;
  }

}

